@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

.chat-app {
  background: white;
  width: 100vw;
  height: 100vh;
  height: 100dvh; /* Dynamic viewport height for mobile browsers */
  overflow: hidden;
  font-family: 'Inter', sans-serif;
  position: relative;
  touch-action: pan-y;
}

.main-container {
  display: flex;
  width: 100%;
  height: 100%;
}

/* Sidebar Styles */
.sidebar {
  background: #f9fafb;
  border-right: 1.25px solid #e5e7eb;
  width: 256px;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  transition: width 0.3s ease, margin-left 0.3s ease, transform 0.3s ease;
  overflow: hidden;
}

.sidebar-closed {
  width: 0;
  margin-left: -256px;
  border-right: none;
}

.sidebar-open {
  width: 256px;
  margin-left: 0;
}

.sidebar-header {
  border-bottom: 1.25px solid #e5e7eb;
  padding: 12px;
}

.menu-button {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  border-radius: 10px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

.menu-button:hover {
  background: rgba(0, 0, 0, 0.05);
}

.menu-button:active {
  background: rgba(0, 0, 0, 0.1);
}

.menu-button2 {
  background: rgba(0, 0, 0, 0.05);
}

.sidebar-content {
  flex: 1;
  padding: 16px 8px;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.navigation {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-bottom: 32px;
}

.nav-button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  border-radius: 10px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  width: 100%;
  text-align: left;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

.nav-button:hover {
  background: rgba(0, 0, 0, 0.05);
}

.nav-button:active {
  background: rgba(0, 0, 0, 0.1);
}

.menu2 {
  background: rgba(0, 0, 0, 0.05);
}

.nav-button img {
  width: 20px;
  height: 20px;
}

.nav-button span {
  font-size: 14px;
  color: #364153;
  font-weight: 400;
  letter-spacing: -0.15px;
}

.section-heading {
  font-size: 12px;
  color: #6a7282;
  font-weight: 500;
  padding: 8px 12px;
}

/* Main Content Styles */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

.header {
  background: white;
  border-bottom: 1.25px solid #e5e7eb;
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-centered {
  justify-content: center;
  position: relative;
}

.header-centered .header-left {
  position: static;
}

.header-centered .user-button {
  position: absolute;
  right: 16px;
}

.header-left {
  display: flex;
  align-items: center;
  gap: 8px;
}

.header-left button,
.header-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 12px;
  border-radius: 10px;
  background: transparent;
  border: none;
  cursor: pointer;
  transition: background 0.2s;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

.header-left button:hover,
.header-button:hover {
  background: rgba(0, 0, 0, 0.05);
}

.header-left button:active,
.header-button:active {
  background: rgba(0, 0, 0, 0.1);
}

.toggle-sidebar-button {
  padding: 8px !important;
}

.toggle-sidebar-button img {
  width: 20px;
  height: 20px;
}

.header-left button:hover {
  background: rgba(0, 0, 0, 0.05);
}

.header-left button span,
.header-button span {
  font-size: 14px;
  color: #364153;
  font-weight: 500;
  letter-spacing: -0.15px;
}

.header-left button img {
  width: 16px;
  height: 16px;
}

.user-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgb(173, 70, 255) 0%, rgb(246, 51, 154) 100%);
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  transition: opacity 0.2s;
}

.user-button:active {
  opacity: 0.8;
}

.user-button img {
  width: 16px;
  height: 16px;
}

.content-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

.center-content {
  max-width: 768px;
  width: 100%;
  padding: 20px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1;
}

.main-heading {
  font-size: 30px;
  font-weight: 500;
  color: #1e2939;
  text-align: center;
  margin-bottom: 48px;
  letter-spacing: 0.4px;
  line-height: 36px;
}

.input-area {
  padding: 16px 20px 20px;
  background: white;
  border-top: 1px solid #e5e7eb;
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  padding-bottom: calc(20px + env(safe-area-inset-bottom));
}

.input-container {
  background: white;
  border: 1.25px solid #d1d5dc;
  border-radius: 24px;
  padding: 10px 17px;
  display: flex;
  align-items: center;
  gap: 12px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.1), 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
  margin-bottom: 8px;
}

.icon-button {
  width: 20px;
  height: 20px;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

.icon-button img {
  width: 100%;
  height: 100%;
}

.input-field {
  flex: 1;
  border: none;
  outline: none;
  font-size: 14px;
  color: #364153;
  font-family: 'Inter', sans-serif;
  letter-spacing: -0.15px;
}

.input-field::placeholder {
  color: #99a1af;
}

.send-button {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #d1d5dc;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  transition: background 0.2s;
}

.send-button img {
  width: 20px;
  height: 20px;
}

.footer-text {
  font-size: 12px;
  color: #6a7282;
  text-align: center;
  line-height: 16px;
}

/* Get Plus Button */
.get-plus-button {
  position: absolute;
  top: 12px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(to right, #ad46ff, #f6339a);
  color: white;
  border: none;
  border-radius: 50px;
  padding: 6px 16px;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: opacity 0.2s;
  z-index: 10;
}

.get-plus-button:hover {
  opacity: 0.9;
}

.get-plus-button img {
  width: 16px;
  height: 16px;
}

.get-plus-button span {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.15px;
}

/* Icon styles */
.icon {
  width: 20px;
  height: 20px;
  display: block;
}

/* Chat Messages Styles */
.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  -webkit-overflow-scrolling: touch;
}

.message-question {
  display: flex;
  justify-content: flex-end;
  width: 100%;
}

.message-question .message-content {
  background: #f3f4f6;
  color: #1e2939;
  padding: 12px 16px;
  border-radius: 18px;
  max-width: 80%;
  word-wrap: break-word;
  font-size: 14px;
  line-height: 1.5;
}

.message-answer {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

.message-answer .message-content {
  background: white;
  color: #1e2939;
  padding: 12px 16px;
  border-radius: 18px;
  max-width: 80%;
  word-wrap: break-word;
  font-size: 14px;
  line-height: 1.5;
  border: 1px solid #e5e7eb;
}

/* Typing Indicator */
.typing-indicator {
  display: flex;
  gap: 4px;
  align-items: center;
  padding: 8px 0;
}

.typing-indicator span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #9ca3af;
  animation: typing 1.4s infinite;
}

.typing-indicator span:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typing {
  0%, 60%, 100% {
    opacity: 0.3;
    transform: translateY(0);
  }
  30% {
    opacity: 1;
    transform: translateY(-8px);
  }
}

/* Send button states */
.send-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.send-button:not(:disabled):hover {
  background: #b8bec7;
}

/* ============================================
   RESPONSIVE DESIGN - MOBILE & TABLET
   ============================================ */

/* Sidebar overlay for mobile */
.sidebar-overlay {
  display: none;
}

/* Tablet and below (768px) */
@media (max-width: 768px) {
  /* Sidebar overlay backdrop */
  .sidebar-overlay {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
    animation: fadeIn 0.3s ease;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  /* Sidebar becomes overlay on mobile */
  .sidebar {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1000;
    height: 100vh;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.1);
  }

  .sidebar-closed {
    transform: translateX(-100%);
    margin-left: 0;
    width: 256px;
  }

  .sidebar-open {
    transform: translateX(0);
    margin-left: 0;
  }

  /* Main content takes full width */
  .main-content {
    width: 100%;
    margin-left: 0 !important;
  }

  /* Header adjustments */
  .header {
    padding: 10px 12px;
  }

  .header-centered {
    justify-content: center;
  }

  .header-centered .user-button {
    position: absolute;
    right: 12px;
  }

  .header-left button span,
  .header-button span {
    font-size: 13px;
  }

  .toggle-sidebar-button {
    display: flex !important;
  }

  /* Main heading */
  .main-heading {
    font-size: 24px;
    line-height: 32px;
    margin-bottom: 32px;
  }

  /* Center content */
  .center-content {
    padding: 16px;
  }

  /* Chat messages */
  .chat-messages {
    padding: 16px 12px;
  }

  .message-question .message-content,
  .message-answer .message-content {
    max-width: 85%;
    font-size: 14px;
    padding: 10px 14px;
  }

  /* Input area */
  .input-area {
    padding: 12px 12px 16px;
  }

  .input-container {
    padding: 8px 12px;
  }

  .input-field {
    font-size: 16px; /* Prevents zoom on iOS */
  }

  /* Footer text */
  .footer-text {
    font-size: 11px;
  }

  /* Section heading */
  .section-heading {
    font-size: 11px;
  }

  /* Navigation buttons */
  .nav-button span {
    font-size: 13px;
  }
}

/* Mobile phones (480px) */
@media (max-width: 480px) {
  /* Header */
  .header {
    padding: 8px 10px;
  }

  .header-centered .user-button {
    right: 10px;
  }

  .header-left button span,
  .header-button span {
    font-size: 12px;
    max-width: 150px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .user-button {
    width: 28px;
    height: 28px;
  }

  .user-button img {
    width: 14px;
    height: 14px;
  }

  /* Main heading */
  .main-heading {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 24px;
    padding: 0 10px;
  }

  /* Chat messages */
  .chat-messages {
    padding: 12px 8px;
    gap: 12px;
  }

  .message-question .message-content,
  .message-answer .message-content {
    max-width: 90%;
    font-size: 14px;
    padding: 8px 12px;
  }

  /* Input area */
  .input-area {
    padding: 10px 10px 14px;
  }

  .input-container {
    padding: 6px 10px;
    gap: 8px;
  }

  .input-field {
    font-size: 16px;
  }

  .send-button {
    width: 28px;
    height: 28px;
  }

  .send-button img {
    width: 18px;
    height: 18px;
  }

  .icon-button {
    width: 18px;
    height: 18px;
  }

  /* Sidebar */
  .sidebar {
    width: 280px;
    max-width: 85vw;
  }

  .sidebar-closed {
    width: 280px;
    max-width: 85vw;
  }

  .sidebar-header {
    padding: 10px;
  }

  .sidebar-content {
    padding: 12px 6px;
  }

  .nav-button {
    padding: 8px 10px;
    gap: 10px;
  }

  .nav-button span {
    font-size: 13px;
  }

  .nav-button img {
    width: 18px;
    height: 18px;
  }

  /* Section heading */
  .section-heading {
    font-size: 11px;
    padding: 6px 10px;
  }
}

/* Small mobile (360px) */
@media (max-width: 360px) {
  .main-heading {
    font-size: 18px;
    line-height: 26px;
  }

  .header-left button span,
  .header-button span {
    font-size: 11px;
    max-width: 120px;
  }

  .message-question .message-content,
  .message-answer .message-content {
    font-size: 13px;
    padding: 7px 10px;
  }

  .input-field {
    font-size: 14px;
  }

  .footer-text {
    font-size: 10px;
  }
}

/* Landscape mobile phones */
@media (max-height: 500px) and (orientation: landscape) {
  .main-heading {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .center-content {
    padding: 12px;
  }

  .chat-messages {
    padding: 10px;
  }

  .input-area {
    padding: 8px 12px;
  }

  .header {
    padding: 6px 12px;
  }
}

/* Tablet landscape (1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .sidebar {
    width: 220px;
  }

  .main-heading {
    font-size: 26px;
  }

  .center-content {
    max-width: 680px;
  }

  .chat-messages {
    max-width: 680px;
  }

  .input-area {
    max-width: 680px;
  }
}

/* Large desktop (1440px+) */
@media (min-width: 1440px) {
  .center-content {
    max-width: 900px;
  }

  .chat-messages {
    max-width: 900px;
  }

  .input-area {
    max-width: 900px;
  }

  .main-heading {
    font-size: 34px;
  }
}

/* Print styles */
@media print {
  .sidebar,
  .header,
  .input-area {
    display: none;
  }

  .chat-messages {
    max-width: 100%;
    padding: 0;
  }
}
